import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import 'lib-flexible';
import 'font-awesome/css/font-awesome.min.css';
//引入axios
import axios from 'axios'
import VueAxios from 'vue-axios'

import {
  Loading,
  Tab,
  Tabs,
  NavBar,
  Icon,
  Search,
  Swipe,
  SwipeItem,
  Card,
  Toast,
  Empty,
  Tag,
  Button,
  Rate,
  Popup,
  Cell,
  Lazyload,
  Stepper,
  Slider,
  Sidebar,
  SidebarItem,
  List,
  Grid,
  GridItem,
  Form,
  Field,
  Dialog
} from 'vant';
Vue.use(Loading)
  .use(Tab)
  .use(Tabs)
  .use(NavBar)
  .use(Icon)
  .use(Search)
  .use(Swipe)
  .use(SwipeItem)
  .use(Card)
  .use(Toast)
  .use(Empty)
  .use(Tag)
  .use(Button)
  .use(Rate)
  .use(Popup)
  .use(Cell)
  .use(Lazyload)
  .use(Stepper)
  .use(Slider)
  .use(Sidebar)
  .use(SidebarItem)
  .use(List)
  .use(Grid)
  .use(GridItem)
  .use(Form)
  .use(Field)
  .use(Dialog)

//注册axios
Vue.use(VueAxios, axios)
//配置请求基础路径
axios.defaults.baseURL = 'https://www.kangliuyong.com:10002'
//配置post请求头
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=utf-8';
//发起请求之前处理
axios.interceptors.request.use(config => {
  if (config.method == 'post') {
    let paramsString = '';
    //post请求参数序列化，转化一个字符串
    for (let key in config.data) {
      paramsString += key + '=' + config.data[key] + '&'
    }
    config.data = paramsString.slice(0, -1);
  }
  return config;
})
Vue.config.productionTip = false
//添加appkey属性保存appkey
Vue.prototype.appkey = 'U2FsdGVkX19WSQ59Cg+Fj9jNZPxRC5y0xB1iV06BeNA=';

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
