export const mutations = {
  changeData(state, data) {
    //data：载荷
    //data.key：修改数据的名称
    //data.value：修改的数据
    if (state[data.key].length > 0) {
      // 有数据的话 判断
      if (state[data.key].indexOf(data.value) !== -1) {
        // 有相同的，先删除 再添加
        state[data.key].splice(state[data.key].indexOf(data.value), 1);
        state[data.key].unshift(data.value);
      } else {
        // 没有相同的 添加
        state[data.key].unshift(data.value);
      }
    } else {
      // 没有数据 添加
      state[data.key].unshift(data.value);
    }
    // state[data.key] = data.value
  },
  changeHome(state, data) {
    state[data.key] = data.value
  },
}